import React from 'react';
import Navbar from './Navbar';
import Sidebar from './Sidebar';
import * as styles from '../styles/DashboardLayout.scss';

const DashboardLayout = ({ children }) => {
	return (
		<div>
			<Navbar />
			<div>
				<Sidebar />
				<main>{children}</main>
			</div>
		</div>
	);
};

export default DashboardLayout;
